@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap');

body {
    margin: 0;
}


* {
    font-family: 'Noto Sans', sans-serif !important;
}

.noScroll {
    overflow-x: hidden;
}


.section-title {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    margin-bottom: 20px;
}


.section-title::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(50% - 0.5px);
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    z-index: 1;
}

.section-title-dark::after {
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.work-display {
    position: relative;
    overflow: hidden;
    max-width: 100%;
    width: 100%;
    display: block;
}

.work-display-link {
    position: relative;
    overflow: hidden;
    max-width: 100%;
    width: 100%;
    height: 100%;
    max-height: 100%;
    display: block;
}

.work-display-link::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
}

.work {
    position: relative;
    overflow: hidden;
    max-width: 100%;
    width: 100%;
    display: block;
}

.workLink {
    position: relative;
    overflow: hidden;
    max-width: 100%;
    width: 100%;
    height: 100%;
    max-height: 100%;
    display: block;
}

.workLink::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
}

.work p {
    position: absolute;
    color: black;
    z-index: 2;
}

.work-main p {
    bottom: 10px;
    left: 0;
    font-size: 24px;
    background: white;
    background: linear-gradient(270deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(255, 255, 255, 0) 100%);
    width: 80%;
    text-align: right;
    padding-right: 10px;
    border-radius: 0 3px 3px 0;

}

.work-display p {
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    font-size: 20px;
    background: rgba(255, 255, 255, 0.7);
    text-align: center;
    padding: 10px;
}

.work img {
    width: 100%;
    height: 200px;
    object-position: center;
    object-fit: cover;
    transition: 1s;
    display: block;
}


@media (min-width: 1000px) {
    .work:hover img {
        transform: scale(1.5);
    }

    .workLink {
        border-radius: 10px;
    }

    .work img {
        height: 300px;

    }
}


@media (max-width: 1000px) {
    .smallPadding {
        margin-left: 20px;
        margin-right: 20px
    }
}



.info {
    display: flex;
    flex-direction: row;
    padding: 40px 20px 0 20px;
    justify-content: center;
    align-items: center;
}

.infoImgDiv {
    margin-right: 20px;
    margin-bottom: 0;

}

@media (max-width: 600px) {
    .info {
        flex-direction: column;
    }

    .infoImgDiv {
        margin-right: 0;
        margin-bottom: 20px;

    }
}

