.page {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%
}

.page-enter {
    opacity: 0;
    transform: scale(1.1);
}

.page-enter-active {
    opacity: 1;
    transform: translate(0, 0);
    transition-duration: 200ms;
}

.page-exit {
    opacity: 1;
    transform: scale(1);
}

.page-exit-active {
    opacity: 0;
    transform: scale(0.9);
    transition-duration: 200ms;
}

.active-button-day {
    background-color: black !important;
    color: white !important;
    border-color: black !important;
}

.active-button-night {
    background-color: white !important;
    color: #121212 !important;
    border-color: white !important;
}

.date {
    position: absolute;
    top: -12px;
    left: 50%;
    text-align: center;
    background-color: white;
    color: black;
    padding: 5px 10px;
    border-radius: 100px;
    transform: translate(-50%, -6px);
    height: 24px;
    box-shadow: 0px 0px 10px 0.1px rgba(0, 0, 0, 0.1);

}

.dateNight {
    background-color: #121212;
    border: 0.1px solid rgba(255,255,255,0.2);
    color: white;
}


.item img {
    margin: 20px 10px;
    border-radius: 1000px;
    width: 150px;
    height: 150px;
    max-width: 150px;
    max-height: 150px;
    min-width: 150px;
    min-height: 150px;
    object-fit: cover;
    /* offset-x | offset-y | blur-radius | spread-radius | color */
    box-shadow: 0px 0px 10px 0.1px rgba(0, 0, 0, 0.1);
    /* border: 1px solid rgba(0, 0, 0, 0.1); */

}

.item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 20px;
    border-radius: 10px;
    margin: 10px 30px 10px 0;
}

.item:not(:last-child):after {
    content: "â¬…";
    color: rgba(0, 0, 0, 0.3) !important;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 105px;
    right: -30px;
    position: absolute;
    transform: translateY(-50%);
}

.itemNight:not(:last-child):after {
    color: rgba(255, 255, 255, 0.3) !important;
}

.blogItem {
    padding: 10px 20px;
    border-radius: 10px;
}

@media (max-width: 1000px) {
    .blogItem {
        border-radius: 0;
    }
}

.ul a {
    text-decoration: none;
    color: steelblue;
}

.ul {
    list-style: none;
}

.ul li {
    margin-bottom: 10px;
}

.ul li::before {
    content: "\2022";
    color: steelblue;
    font-weight: bold;
    display: inline-block;
    width: 1em;
    margin-left: -1em;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.noMarginBottom {
    margin-bottom: 0 !important;
}

::-webkit-scrollbar {
    -webkit-appearance: none;
    height: 7px;
}

::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(0, 0, 0, .3);
}